.login {
  background-color:#1d1d4c;

  header,footer {
    .header-container {
      width:1060px;
    }
  }

  .contents-inner {
    width:1060px;
  }

  .login-form {
    margin-top:40px;
    background: url(../images/illust-login.png) no-repeat left bottom 94px;

    .form-cnt {
      position:relative;
      width: 420px;
      height: 660px;
      float:right;
      padding: 60px 40px 40px;
      border-radius: 5px;
      box-shadow: 0 0 10px 0 rgba(15, 15, 21, 0.05);
      background-color: #fff;

      &.pd-desc {
        padding:0;
      }

      .login-tit {
        padding-top:20px;
        font-size:24px;
        color:#0f0f15;
        font-weight:500;
      }

      .form-input {
        margin-top:40px;
      }

      .login-helper {
        position:absolute;
        width:100%;
        bottom:40px;
        left:50%;
        transform:translateX(-50%);
        text-align:center;
        line-height:1.57;
        color:#57576d;

        a {
          font-weight:500;
          color:#0f0f15;
          padding-left:10px;

          &:hover {
            color:#23527c;
          }
        }

        .find-info {
          margin-bottom:5px;
          a {
            padding-right:14px;
          }
        }
      }

      .form-tab {
        padding-top:40px;
        border-bottom:1px solid #f4f5f6;

        .tabs-line {
          width:80%;
          margin:0 auto;
          border-bottom:0;
        }
      }

      .find-txt {
        padding:18px 40px;
        color:#57576d;
        border-bottom:1px solid #f4f5f6;
      }

      .find-info {
        border-bottom:1px solid #f4f5f6;

        .find-type {
          padding:20px 40px;
        }

        .find-info-input {
          display:none;
          padding:0 0 40px 74px;

          &.active {
            display:block;
          }
        }
      }

      .find-btn {
        position:absolute;
        bottom:60px;
        left:50%;
        transform:translateX(-50%);
      }
      .page-back {
        position:absolute;
        width:340px;
        bottom:30px;
        left:50%;
        transform:translateX(-50%);
        text-align:right;
        font-weight:500;
      }

      .find-card {
        height:446px;
        padding:30px 40px;

        .card-tit {
          font-size:16px;
          color:#57576d;
        }

        &.result {
          background:url(../images/illust-confirm.png) no-repeat bottom center;
        }
        &.send {
          background:url(../images/illust-email.png) no-repeat bottom center;
        }
      }

      .reset-pw-input {
        padding:18px 40px;

        .reset-txt {
          color:#57576d;
          margin-bottom:30px;
        }
      }
    }
  }
}